}

.navbar {
  display: flex;
  justify-content: space-evenly;
  background-image: -webkit-gradient(linear, left top, left bottom, from(blue), color-stop(blue), to(white));
  background-image: -webkit-linear-gradient(blue, blue, white);
  background-image: linear-gradient(blue, blue, white);
  padding-bottom: 40px;
}

ul.navbar {
  margin-top: 0px;
  margin-bottom: 0px;
}

